import "core-js/modules/es.iterator.constructor.js";
import "core-js/modules/es.iterator.find.js";
import "core-js/modules/es.iterator.flat-map.js";
import "core-js/modules/es.iterator.for-each.js";
import "core-js/modules/es.iterator.reduce.js";
import "core-js/modules/es.set.difference.v2.js";
import "core-js/modules/es.set.intersection.v2.js";
import "core-js/modules/es.set.is-disjoint-from.v2.js";
import "core-js/modules/es.set.is-subset-of.v2.js";
import "core-js/modules/es.set.is-superset-of.v2.js";
import "core-js/modules/es.set.symmetric-difference.v2.js";
import "core-js/modules/es.set.union.v2.js";
export default {
  data() {
    return {
      surveyData: {},
      currentPage: 0,
      answers: {},
      requiredQuestions: new Set()
    };
  },
  computed: {
    currentQuestions() {
      return this.surveyData.pages?.[this.currentPage]?.questions || [];
    },
    totalPages() {
      return this.surveyData.pages?.length || 0;
    }
  },
  async mounted() {
    // 加载问卷配置
    const response = await fetch('/question.json');
    this.surveyData = await response.json();
    // 初始化必答问题集合
    this.surveyData.pages.forEach(page => {
      page.questions.forEach(q => this.requiredQuestions.add(q.id));
    });
  },
  methods: {
    handleAnswer(questionId) {
      this.requiredQuestions.delete(questionId);
    },
    prevPage() {
      this.currentPage = Math.max(0, this.currentPage - 1);
    },
    nextPage() {
      if (this.requiredQuestions.size > 0) {
        this.$message.error('请完成本页所有题目');
        return;
      }
      this.currentPage = Math.min(this.totalPages - 1, this.currentPage + 1);
    },
    async submitSurvey() {
      // 计算得分
      const total = this.surveyData.pages.flatMap(page => page.questions).length;
      const correct = Object.entries(this.answers).reduce((acc, [id, answer]) => {
        const question = this.findQuestion(Number(id));
        return acc + (answer === question.correct ? 1 : 0);
      }, 0);

      // 构建提交数据
      const result = {
        score: `${correct}/${total}`,
        answers: this.answers,
        timestamp: new Date().toISOString()
      };

      // 发送邮件（需替换为实际API）
      try {
        await this.sendEmail(result);
        this.$message.success('提交成功！结果已发送');
      } catch (error) {
        this.$message.error('提交失败，请重试');
      }
    },
    findQuestion(id) {
      return this.surveyData.pages.flatMap(page => page.questions).find(q => q.id === id);
    },
    async sendEmail(data) {
      // 实际应调用后端API发送邮件
      console.log('模拟发送到:', this.surveyData.email);
      console.log('邮件内容:', JSON.stringify(data, null, 2));

      // 示例：使用 mailto 链接
      const subject = `问卷结果 - 得分 ${data.score}`;
      const body = `
          得分：${data.score}
          完整答案：${JSON.stringify(data.answers, null, 2)}
        `;
      window.location.href = `mailto:${this.surveyData.email}?subject=${encodeURIComponent(subject)}&body=${encodeURIComponent(body)}`;
    }
  }
};